import React from "react";
import Autocomplete from "./Autocomplete";

type Props = {
  value: string,
  values: string[],
  select: (s: string) => void
};

export const DistrictSelect = ({ value, values, select }: Props) => (
  <Autocomplete value={value} options={values} onChange={select} />
);
