// @flow

import React, { Component } from "react";
import TextToSVG from "text-to-svg";
import pathCommandToString from "./pathCommandToString";

import { otfBebas } from "../../../theme";
import { parseSVG, makeAbsolute } from "svg-path-parser";
import Label from "./Label";

type Props = {
  color: string,
  x: number,
  y: number,
  text: string,
  width: number
};

// дописать оставшиеся типы и сделать пулл-реквест в библиотеку
type TextToSvgOptions = {
  x?: number,
  y?: number,
  fontSize?: number,
  kerning?: boolean,
  letterSpacing?: number,
  tracking?: number,
  anchor?: string
};

type TextToSvgInstance = {
  getMetrics: (
    text: string,
    options?: TextToSvgOptions
  ) => {
    x: number,
    y: number,
    baseline: number,
    width: number,
    height: number,
    ascender: number,
    descender: number
  },
  getD: (text: string, options?: TextToSvgOptions) => string
};

const ASPECT = 297 / 210;
const CONCAVENESS = 0.3;

const createDistort = (width, height, factor, baseScale) => (x, y) => {
  const topGap = height * 0.07;
  const xFact = x / width;
  const yFact = baseScale + factor * (1 - Math.sin(Math.PI * xFact));

  return [x, (y <= topGap * 2 ? y : y * yFact) + topGap * (1 / 0.8)];
};

export default class District extends Component<Props> {
  textToSvg: TextToSvgInstance | null = null;

  constructor(props: Props) {
    super(props);

    TextToSVG.load(otfBebas, (err, textToSVG) => {
      if (err) {
        console.error(err);
      }

      this.textToSvg = textToSVG;
    });
  }

  render() {
    const { color, x, y, text, width } = this.props;

    if (this.textToSvg && text && width) {
      const tWidth = width;
      const { textToSvg } = this;

      const metrics = textToSvg.getMetrics(text, {
        fontSize: tWidth
      });

      const factor = tWidth / metrics.width;
      const fontSize = tWidth * factor;
      const stretch = Math.min(
        1 + 12 / text.length,
        metrics.width / (ASPECT * metrics.height - 1)
      );

      const distort = createDistort(
        tWidth,
        metrics.height * factor,
        stretch * CONCAVENESS,
        stretch * (1 - CONCAVENESS)
      );

      const d = textToSvg.getD(text, {
        fontSize,
        anchor: "left top",
        x: 0,
        y: 0
      });

      const ast = parseSVG(d);

      makeAbsolute(ast);

      const newD = ast.reduce(
        (acc, part) => `${acc} ${pathCommandToString(part, distort)}`,
        ""
      );

      const logoOffset = fontSize * stretch * CONCAVENESS;

      return (
        <g>
          <path d={newD} fill={color} transform={`translate(${x} ${y})`} />
          <Label
            color={color}
            width={297}
            x={0}
            y={
              y -
              metrics.height * factor * -0.05 +
              metrics.height * factor * 0.8 * stretch +
              (logoOffset > 40 ? -22 : 0)
            }
          />
        </g>
      );
    }

    return <g />;
  }
}
