// @flow

import React from "react";
import Template from "./Template";
import District from "./District";

// это менять только в крайнем случае
const CANVAS_WIDTH = 297;

const BIG_FZ = 50;
const MEDIUM_FZ = 42;
const SMALL_FZ = 35;
const OFFSET_RIGHT = 10;
const BASE_TEMPLATE_OFFSET = 32;
const WORDS_TO_REPLACE = [
  { value: "бульвар", size: 7.4 },
  { value: "улица", size: 11.8 },
  { value: "проспект", size: 7.8 },
  { value: "площадь", size: 6.5 },
  { value: "шоссе", size: 11.8 },
  { value: "восточное", size: 5.8 },
  { value: "западное", size: 6.2 },
  { value: "северное", size: 7.8 },
  { value: "южное", size: 10 },
  { value: "верхние", size: 9 },
  { value: "нижние", size: 9 },
  { value: "центральное", size: 5.7 }
].map(({ value, size }) => ({
  value: value.toUpperCase(),
  size
}));

export default class Doshirak extends React.Component<*> {
  render() {
    const {
      style,
      className,
      color,
      district: district = "",
      svgRef
    } = this.props;

    let text = district.toUpperCase(),
      label;

    for (let i = 0; i < WORDS_TO_REPLACE.length; i++) {
      let word = WORDS_TO_REPLACE[i];

      if (!label && text.includes(word.value)) {
        label = word;
        text = text.replace(word.value, "").trim();
      }
    }

    const twoColumns = text.length > 8;
    const bigFz = !twoColumns && text.length > 6 ? MEDIUM_FZ : BIG_FZ;
    const fz = twoColumns ? SMALL_FZ : bigFz;
    const textWidth = twoColumns ? fz * 1.92 : fz * 0.91;
    const xTextStart = CANVAS_WIDTH - OFFSET_RIGHT;
    const xTextEnd = xTextStart - textWidth;
    const labelFz = label
      ? label.size * (twoColumns ? (2 * SMALL_FZ) / bigFz : 1)
      : 0;

    return (
      <svg
        viewBox={`0 0 ${CANVAS_WIDTH} 420`}
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        ref={svgRef}
        className={className}
        style={style}
      >
        <Template
          color={color}
          transform={`translate(${xTextEnd - BASE_TEMPLATE_OFFSET} 0)`}
        />

        <rect
          x={xTextStart + fz * 0.035 - textWidth}
          y={0}
          width={textWidth}
          height={1}
          fill={color}
        />

        <District
          fz={fz}
          value={text}
          twoColumns={twoColumns}
          x={xTextStart - textWidth}
          y={label ? label.size * 1.2 + 5 : 3}
          color={color}
        />

        {label && (
          <text
            fill={color}
            y={5 + 0.7 * labelFz}
            x={xTextEnd}
            width={textWidth}
            alignmentBaseline="alphabetic"
            dominantBaseline="alphabetic"
            fontFamily="Montserrat"
            fontStyle="italic"
            fontWeight="900"
            fontSize={labelFz}
          >
            {label.value}
          </text>
        )}
      </svg>
    );
  }
}
