// @flow

import React from "react";
import Tabs from "@material-ui/core/es/Tabs";
import Tab from "@material-ui/core/es/Tab";

type Value = {
  label: string,
  value: string
};

type Props = {
  selected: string,
  values: Value[],
  select: (s: string) => void
};

export const PropertySelector = ({ selected, values, select }: Props) => (
  <Tabs
    value={selected}
    onChange={(event, value) => select(value)}
    indicatorColor="primary"
    textColor="primary"
  >
    {values.map((val, i) => (
      <Tab label={val.label} value={val.value} key={i} />
    ))}
  </Tabs>
);
