import { isNil } from "ramda";

const unwrap = val => (isNil(val) ? "" : ` ${val}`);

const pathCommandToString = (command, distort) => {
  const unwrapP = (x, y) =>
    isNil(x) || isNil(y) ? "" : ` ${distort(x, y).join(",")}`;

  const {
    code,
    x,
    y,
    x0,
    y0,
    x1,
    x2,
    y1,
    y2,
    rx,
    ry,
    largeArc,
    sweep,
    xAxisRotation
  } = command;
  //--- Z, V, H ---
  if (code === "Z") return " Z";
  if (isNil(x)) {
    if (isNil(y)) return ` ${code}`;
    return `${code} ${distort(x0, y)[1]}`;
  }
  if (isNil(y)) return `${code} ${distort(x, y0)[0]}`;
  //--- Z, V, H end ---

  return `${code} ${unwrap(rx)}${unwrap(ry)}${unwrap(xAxisRotation)}${unwrap(
    largeArc
  )}${unwrap(sweep)}${unwrapP(x1, y1)}${unwrapP(x2, y2)} ${distort(x, y).join(
    ","
  )}`;
};

export default pathCommandToString;
