// @flow

import React, { Component } from "react";
import Grid from "@material-ui/core/es/Grid";

import { colors, districts, sizes } from "../../../data";
import { tshirtColors } from "../../theme";
import { InterfaceContainer, Print, TShirt, TShirtWrapper } from "./styled";
import Form from "./Form";
import { isInViewPort } from "../../utils";

const title = "TXTL ЭКСПРМНТЛ #2";

const foreignCities = ["La", "Bklyn", "Nyc"].map(v => ({ value: v, label: v }));

const getPrintColors = color => {
  switch (color.value) {
    case "white":
      return {
        printColor: "#f00",
        logoColor: "#f00",
        stripeTextColor: "#fff",
        stripeBgColor: "#000"
      };
    case "blue":
      return {
        printColor: tshirtColors.pink,
        logoColor: tshirtColors.pink,
        stripeTextColor: tshirtColors.blue2,
        stripeBgColor: "#fff"
      };
    case "red":
      return {
        printColor: "#fff",
        logoColor: "#fff",
        stripeTextColor: "#000",
        stripeBgColor: tshirtColors.yellow
      };
    default:
      return {
        printColor: "#fff",
        logoColor: "#fff",
        stripeTextColor: "#000",
        stripeBgColor: tshirtColors.orange
      };
  }
};

export default class La extends Component<
  { className?: string, style?: any },
  *
> {
  state = {
    district: districts[0],
    color: colors[0],
    size: sizes[2],
    foreign: foreignCities[0]
  };

  rotate: number = 0;

  svgRef: { current: any } = React.createRef();
  slideIntervalId = -1;

  startSlideShow() {
    let color = 0;
    let foreign = 0;
    this.slideIntervalId = window.setInterval(() => {
      if (isInViewPort(this.svgRef)) {
        color = color < colors.length - 1 ? color + 1 : 0;
        foreign = foreign < foreignCities.length - 1 ? foreign + 1 : 0;
        this.setState({
          color: colors[color],
          foreign: foreignCities[foreign],
          district: districts[Math.round(Math.random() * districts.length - 2)]
        });
      }
    }, 2000);
  }

  stopSlideShow() {
    window.clearInterval(this.slideIntervalId);
    this.slideIntervalId = -1;
  }

  componentDidMount() {
    this.startSlideShow();
  }

  componentWillUnmount() {
    this.stopSlideShow();
  }

  selectDistrict(selected: string) {
    this.setState({ district: selected });
  }

  selectColor(selected: string) {
    const result = colors.find(item => item.value === selected);
    this.setState({ color: result });
  }

  selectSize(selected: string) {
    const result = sizes.find(item => item.value === selected);
    this.setState({ size: result });
  }

  selectForeign(selected: string) {
    const result = foreignCities.find(item => item.value === selected);
    this.setState({ foreign: result });
  }

  buy() {
    const { color, district, size, foreign } = this.state;
    let print;

    try {
      print = this.svgRef.current.outerHTML;
    } catch (e) {
      console.log(
        "Ошибка при получении принта. Попробуйте, пожалуйста, еще раз"
      );
    }

    const args = {
      name: `${title} ${foreign.value} ${district}${this.rotate}`.toUpperCase(),
      color: color.label,
      size: size.label,
      print
    };
    if ("txtlByu" in window) {
      window.txtlByu(...Object.values(args));
    } else {
      console.log(args);
    }
  }

  render() {
    const { color, district, size, foreign } = this.state;
    this.rotate = Math.round(25 * (Math.random() - 0.5));
    return (
      <div className={this.props.className} style={this.props.style}>
        <Grid container justify="flex-start" alignItems="center">
          <Grid item xs={12} md={8}>
            <TShirtWrapper>
              <TShirt color={color.value}>
                <Print
                  rotate={this.rotate}
                  foreign={foreign.value}
                  district={district}
                  svgRef={this.svgRef}
                  {...getPrintColors(color)}
                />
              </TShirt>
            </TShirtWrapper>
          </Grid>
          <InterfaceContainer item xs={12} md={4}>
            <Form
              foreignCities={foreignCities}
              foreign={foreign.value}
              district={district}
              color={color.value}
              size={size.value}
              onClick={() => this.stopSlideShow()}
              selectDistrict={val => this.selectDistrict(val)}
              selectColor={val => this.selectColor(val)}
              selectSize={val => this.selectSize(val)}
              selectForeign={val => this.selectForeign(val)}
              buy={() => this.buy()}
            />
          </InterfaceContainer>
        </Grid>
      </div>
    );
  }
}
