// @flow

import React, { Component } from "react";
import TextField from "@material-ui/core/es/TextField/TextField";
import MenuList from "@material-ui/core/es/MenuList";
import {
  StyledPaper,
  StyledMenuItem,
  StyledSearchIcon,
  Wrapper
} from "./styled";
import { mainBg } from "../../../theme";

type Props = {
  value: string,
  options: string[],
  onChange: (val: string) => void
};

type State = {
  inputVal: string,
  justUpdated: boolean
};

export default class Autocomplete extends Component<Props, State> {
  state = {
    inputVal: this.props.value,
    justUpdated: true
  };

  inputRef = React.createRef<HTMLInputElement>();

  componentDidUpdate() {
    const { value } = this.props;

    if (
      this.inputRef.current !== document.activeElement &&
      this.state.inputVal !== value
    )
      this.setState({ inputVal: value });
  }

  select(val: string) {
    this.setState({
      inputVal: val,
      justUpdated: true
    });

    this.props.onChange(val);
  }

  onInputChange(e: SyntheticInputEvent<HTMLInputElement>) {
    this.setState({
      // фильтруем все символы, которых не может в названиях
      inputVal: [...e.target.value.toLowerCase()]
        .filter(l => /[{a-zа-я0-9- }]/gi.test(l))
        .join(""),
      justUpdated: false
    });
  }

  onKeyUp(e: SyntheticKeyboardEvent<HTMLInputElement>, options: string[]) {
    options[0] && e.key === "Enter" && this.select(options[0]);
  }

  render() {
    const { inputVal, justUpdated } = this.state;

    let options = [];

    try {
      options = this.props.options
        .filter(opt =>
          new RegExp(
            [...inputVal]
              .map(l => (l === "е" || l === "ё" ? "[её]" : l))
              .join(".{0,3}")
          ).test(opt)
        )
        .filter(
          (_, _i, { length }) =>
            (inputVal.length >= 2 || length <= 10) && length >= 1
        );
    } catch (e) {
      console.error(e);
      console.log(inputVal);
    }

    return (
      <Wrapper>
        <TextField
          margin="normal"
          variant="outlined"
          spellCheck="false"
          fullWidth
          value={inputVal}
          onChange={e => this.onInputChange(e)}
          onKeyUp={e => this.onKeyUp(e, options)}
          inputRef={this.inputRef}
        />
        <StyledSearchIcon color={mainBg} />
        <StyledPaper hidden={justUpdated || options.length === 0}>
          <MenuList>
            {options.map(opt => (
              <StyledMenuItem
                disableGutters
                button
                onClick={() => this.select(opt)}
                key={opt}
                value={opt}
              >
                {opt}
              </StyledMenuItem>
            ))}
          </MenuList>
        </StyledPaper>
      </Wrapper>
    );
  }
}
