// @flow

import React from "react";
import District from "./District";

type Props = {
  style?: any,
  className?: string,
  svgRef: { current: any },
  color: string,
  district: string
};

// это менять только в крайнем случае
const CANVAS_WIDTH = 297;

const STRIPE_WIDTH = CANVAS_WIDTH;

export default ({ style, className, svgRef, color, district }: Props) => {
  return (
    <svg
      viewBox={`0 0 ${CANVAS_WIDTH} 420`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      ref={svgRef}
      className={className}
      style={style}
    >
      <District
        color={color}
        x={25}
        y={50}
        width={STRIPE_WIDTH - 50}
        text={district}
      />
    </svg>
  );
};
