// @flow

import Bklyn from "./Bklyn";
import La from "./La";
import Nyc from "./Nyc";
import React from "react";

type Props = any & { name: string, color1: string, color2: string };

const Foreign = (props: Props) => {
  switch (props.name) {
    case "Bklyn":
      return <Bklyn {...props} />;
    case "La":
      return <La {...props} />;
    case "Nyc":
      return <Nyc {...props} />;
    default:
      return null;
  }
};

export default Foreign;
