// @flow

import React, { Component } from "react";
import TextToSVG from "text-to-svg";

import { otfBebas } from "../../../theme";

type Props = {
  color1: string,
  color2: string,
  x: number,
  y: number,
  text: string,
  width: number,
  rotate: number
};

// дописать оставшиеся типы и сделать пулл-реквест в библиотеку
type TextToSvgOptions = {
  x?: number,
  y?: number,
  fontSize?: number,
  kerning?: boolean,
  letterSpacing?: number,
  tracking?: number,
  anchor?: string
};

type TextToSvgInstance = {
  getMetrics: (
    text: string,
    options?: TextToSvgOptions
  ) => {
    x: number,
    y: number,
    baseline: number,
    width: number,
    height: number,
    ascender: number,
    descender: number
  },
  getD: (text: string, options?: TextToSvgOptions) => string
};

export default class Stripe extends Component<Props> {
  textToSvg: TextToSvgInstance | null = null;

  constructor(props: Props) {
    super(props);

    TextToSVG.load(otfBebas, (err, textToSVG) => {
      if (err) {
        console.error(err);
      }

      this.textToSvg = textToSVG;
    });
  }

  render() {
    const { color1, color2, x, y, text, width, rotate } = this.props;
    let height = 0;

    if (this.textToSvg && text && width) {
      const tWidth = width * 0.8;
      const { textToSvg } = this;

      const metrics = textToSvg.getMetrics(text, {
        fontSize: tWidth
      });

      const factor = tWidth / metrics.width;
      const fontSize = Math.min(tWidth * factor, 30);

      height = fontSize * 1.1;

      const d = textToSvg.getD(text, {
        fontSize,
        anchor: "center center",
        x: 0,
        y: 0
      });

      return (
        <g transform={`translate(${x} ${y})`}>
          <g transform={`rotate(${rotate})`}>
            <rect
              x={-width / 2}
              y={-height * 0.84}
              width={width}
              height={height}
              fill={color2}
            />
            <path d={d} fill={color1} />
          </g>
        </g>
      );
    }

    return <g />;
  }
}
