// @flow

import React from "react";

type Props = {
  value: string,
  fz: number,
  twoColumns: boolean,
  x: number,
  y: number,
  color: string
};

const pairUp = string => {
  const result = [];
  let tmp = "";

  [...string].forEach((c, i) => {
    if (i % 2 === 0) {
      tmp = c;
    } else {
      tmp += c;
      result.push(tmp);
    }
  });

  if (tmp.length === 1) result.push(tmp);

  return result;
};

const District = ({ value, fz, twoColumns, x, y, color }: Props) => {
  const text = value
    .trim()
    .replace(/ /g, " " /* &nbsp; */)
    .toUpperCase();

  return (
    <>
      {(twoColumns ? pairUp(text) : [...text]).map((c, i) => (
        <text
          fill={color}
          fontSize={fz}
          y={y + fz * (i + 1) * 0.87}
          x={x}
          key={i}
          fontFamily="txtl"
          alignmentBaseline="alphabetic"
          dominantBaseline="alphabetic"
        >
          {c}
        </text>
      ))}
    </>
  );
};

export default District;
