// @flow

import * as React from "react";
import { Container, Print, Image } from "./styled";
import black from "./black.jpg";
import blue from "./blue.jpg";
import red from "./red.jpg";
import white from "./white.jpg";

type Props = {
  color: string,
  children?: React.Node,
  className?: string
};

const colors = ["black", "blue", "red", "white"];

const getPicture = color => {
  switch (color) {
    case "black":
      return black;
    case "blue":
      return blue;
    case "red":
      return red;
    default:
      return white;
  }
};

const getPictures = color =>
  colors.map(c => (
    <Image
      key={c}
      src={getPicture(c)}
      style={{ opacity: c === color ? "1" : "0" }}
    />
  ));

export const TShirt = ({ color, children, className }: Props) => (
  <Container className={className}>
    <div>{getPictures(color)}</div>
    <Print>{children}</Print>
  </Container>
);
