// @flow

import React, { Component } from "react";
import { createPortal } from "react-dom";
import Grid from "@material-ui/core/es/Grid";

import { colors, districts, sizes } from "../../../data";
import { tshirtColors } from "../../theme";
import {
  InterfaceContainer,
  Logo,
  Print,
  TShirt,
  TShirtWrapper
} from "./styled";
import Form from "./Form";
import { isInViewPort } from "../../utils";

const title = "TXTL ЭКСПРМНТЛ #3";
const portalRoot = document.querySelector("#exprmntl3-portal");

const getColor = color => {
  switch (color.value) {
    case "white":
      return tshirtColors.blue2;
    case "blue":
      return "#fff";
    case "red":
      return "#fff";
    default:
      return "#fff";
  }
};

export default class La extends Component<
  { className?: string, style?: any },
  *
> {
  state = {
    district: districts[0],
    color: colors[0],
    size: sizes[2]
  };

  svgRef: { current: any } = React.createRef();
  slideIntervalId = -1;

  startSlideShow() {
    let color = 0;
    this.slideIntervalId = window.setInterval(() => {
      if (isInViewPort(this.svgRef)) {
        color = color < colors.length - 1 ? color + 1 : 0;
        this.setState({
          color: colors[color],
          district: districts[Math.round(Math.random() * districts.length - 2)]
        });
      }
    }, 2000);
  }

  stopSlideShow() {
    window.clearInterval(this.slideIntervalId);
    this.slideIntervalId = -1;
  }

  componentDidMount() {
    this.startSlideShow();
  }

  componentWillUnmount() {
    this.stopSlideShow();
  }

  selectDistrict(selected: string) {
    this.setState({ district: selected });
  }

  selectColor(selected: string) {
    const result = colors.find(item => item.value === selected);
    this.setState({ color: result });
  }

  selectSize(selected: string) {
    const result = sizes.find(item => item.value === selected);
    this.setState({ size: result });
  }

  buy() {
    const { color, district, size } = this.state;
    let print;

    try {
      print = this.svgRef.current.outerHTML;
    } catch (e) {
      console.log(
        "Ошибка при получении принта. Попробуйте, пожалуйста, еще раз"
      );
    }

    const args = {
      name: `${title}  ${district}`.toUpperCase(),
      color: color.label,
      size: size.label,
      print
    };
    if ("txtlByu" in window) {
      window.txtlByu(...Object.values(args));
    } else {
      console.log(args);
    }
  }

  render() {
    const { color, district, size } = this.state;
    return portalRoot
      ? createPortal(
          <div className={this.props.className} style={this.props.style}>
            <Grid container justify="flex-start">
              <Grid item xs={12} md={8}>
                <TShirtWrapper>
                  <Logo />
                  <TShirt color={color.value}>
                    <Print
                      svgRef={this.svgRef}
                      district={district}
                      color={getColor(color)}
                    />
                  </TShirt>
                </TShirtWrapper>
              </Grid>
              <InterfaceContainer item xs={12} md={4}>
                <Form
                  district={district}
                  color={color.value}
                  size={size.value}
                  onClick={() => this.stopSlideShow()}
                  selectDistrict={val => this.selectDistrict(val)}
                  selectColor={val => this.selectColor(val)}
                  selectSize={val => this.selectSize(val)}
                  buy={() => this.buy()}
                />
              </InterfaceContainer>
            </Grid>
          </div>,
          portalRoot
        )
      : null;
  }
}
