// @flow

import React from "react";
import Foreign from "./Foreign";
import Stripe from "./Stripe";

type Props = {
  style: any,
  className: string,
  svgRef: { current: any },
  foreign: string,
  printColor: string,
  logoColor: string,
  stripeTextColor: string,
  stripeBgColor: string,
  district: string,
  rotate: number
};

// это менять только в крайнем случае
const CANVAS_WIDTH = 297;

const STRIPE_WIDTH = CANVAS_WIDTH - 50;

export default ({
  style,
  className,
  svgRef,
  foreign,
  printColor,
  logoColor,
  stripeTextColor,
  stripeBgColor,
  district,
  rotate
}: Props) => {
  return (
    <svg
      viewBox={`0 0 ${CANVAS_WIDTH} 420`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      ref={svgRef}
      className={className}
      style={style}
    >
      <Foreign name={foreign} color1={printColor} color2={logoColor} />
      <Stripe
        color1={stripeTextColor}
        color2={stripeBgColor}
        rotate={rotate}
        x={CANVAS_WIDTH / 2}
        y={110}
        width={STRIPE_WIDTH}
        text={district}
      />
    </svg>
  );
};
